.Footer {
  width: 100%;
  height: 77px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #1c1c1c;
  margin-top: 80px;
}
.FooterText {
  color: #777777;
  margin-left: 28px;
}

/* Media Queries */
@media (max-width: 600px) {
  .Footer {
    justify-content: center;
  }
  .FooterText {
    margin-left: 0;
  }
}
